import { GET_MOVIES } from 'constants/actionTypes';
import { createTmdbUrl, fetchJSON, handleError } from 'utils/utils';

export default function getMovies(moviesListType, page = 1) {
  const requestUrl = createTmdbUrl(`/movie/${moviesListType}?page=${page}`);

  return {
    type: GET_MOVIES,
    payload: (async () => {
      try {
        const movies = await fetchJSON(requestUrl);
        return { movies: movies.results, pages: movies.total_pages };
      } catch (err) {
        return handleError(err.status_message);
      }
    })(),
  };
}



