// @flow
import React, { Component } from 'react';
import { Field, reduxForm } from 'redux-form';
import * as styles from './SearchForm.scss';

const validate = values => {
  const errors = {};

  if (!values.search) {
    errors.search = 'There is nothing to search';
  }

  return errors;
};

type SearchFieldProps = {
  input: any,
  type: string,
  meta: Object,
};

class RenderSearchField extends Component<SearchFieldProps> {
  componentDidMount() {
    this.searchInput.select();
  }

  searchInput: HTMLInputElement;

  render() {
    const {
      input,
      type,
      meta: { active, touched, submitFailed, error },
    } = this.props;
    return (
      <div className={styles.searchFormInputWrapper}>
        <input
          {...input}
          className={styles.searchFormInput}
          placeholder="Search..."
          type={type}
          ref={searchInput => { this.searchInput = searchInput; }}
        />
        {active && touched && submitFailed && error && <span className={styles.searchFormError}>{error}</span>}
      </div>
    );
  }
}

const SearchForm = ({ handleSubmit, submitting }) => (
  <form
    className={styles.searchForm}
    onSubmit={handleSubmit}
  >
    <Field
      name="search"
      type="text"
      component={RenderSearchField}
    />
    <button
      className={styles.searchFormBtn}
      type="submit"
      disabled={submitting}
    >
      Search
    </button>
  </form>
);

export default reduxForm({
  form: 'search',
  validate,
  initialValues: {
    search: '',
  },
})(SearchForm);



