// @flow
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getAppConfig } from 'actions/initActions';
import { toggleNavigation } from 'actions/uiActions';
import { logout } from 'actions/authActions';
import Header from './Header';
import Navigation from './Navigation';
import Main from './Main';
import Footer from './Footer';
import LoadingIndicator from './LoadingIndicator';
import AppInitError from './AppInitError';
import * as styles from './PrivatePagesLayout.scss';

class PrivatePagesLayout extends Component<PrivatePageLayoutProps> {
  componentDidMount() {
    this.props.getAppConfig();
  }

  links: RtmdbNavLink[] = [
    { title: 'Now playing', url: '/now-playing' },
    { title: 'Popular', url: '/popular' },
    { title: 'Top', url: '/top' },
    { title: 'Upcoming', url: '/upcoming' },
    { title: 'Search', url: '/search' },
  ];

  render() {
    const {
      isAppInitialized,
      initializationError,
      isNavigationVisible,
      handleToggleNavigation,
      handleLogout,
      children,
    } = this.props;

    let component;
    if (isAppInitialized && initializationError) {
      component = <AppInitError />;
    } else if (isAppInitialized && !initializationError) {
      component = children;
    }

    return (
      <div className={styles.privatePagesLayout}>
        <Header
          isNavigationVisible={isNavigationVisible}
          onNavigationBtnClick={handleToggleNavigation}
        />
        <Navigation
          isVisible={isNavigationVisible}
          links={this.links}
          onLogout={handleLogout}
        />
        <Main>
          {!isAppInitialized && !initializationError ? <LoadingIndicator /> : null}
          {component}
        </Main>
        <Footer />
      </div>
    );
  }
}

const mapStateToProps = state => ({
  isAppInitialized: state.init.requests === 0,
  initializationError: state.init.error,
  isNavigationVisible: state.ui.isNavigationVisible,
});

const mapDispatchToProps = (dispatch: Function) => ({
  handleToggleNavigation: () => dispatch(toggleNavigation()),
  handleLogout: () => dispatch(logout()),
  getAppConfig: () => dispatch(getAppConfig()),
});

export default connect(mapStateToProps, mapDispatchToProps)(PrivatePagesLayout);



