// @flow
import React, { Component } from 'react';
import MoviesPagination from './MoviesPagination';
import MoviesList from './MoviesList';
import LoadingIndicator from './LoadingIndicator';
import * as styles from './Movies.scss';

class Movies extends Component<MoviesPagesProps, MoviesPagesState> {
  state = {
    activePage: 1,
  };

  componentDidMount() {
    this.changePage(1);
  }

  changePage = (page: number) => {
    this.props.getMovies(page);
    this.setState({ activePage: page });
  };

  render() {
    const movies = this.props.movies;
    const activePage = this.state.activePage;
    return (
      <div className={styles.movies}>
        {movies.isFetching ? <LoadingIndicator /> : null}
        <MoviesPagination
          pagesCount={movies.pages}
          activePage={activePage}
          onPageChange={this.changePage}
        />
        <MoviesList movies={movies.items} />
      </div>
    );
  }
}

export default Movies;



