// @flow
import { GET_APP_CONFIG } from 'constants/actionTypes';
import { fetchJSON, handleError } from 'utils/utils';

export const getAppConfig = () => {
  const { API_HOST, API_VERSION, API_KEY } = process.env.APP_CONFIG;
  const requestUrl = `${API_HOST}/${API_VERSION}/configuration?api_key=${API_KEY}`;

  return {
    type: GET_APP_CONFIG,
    payload: (async () => {
      try {
        const tmdbCfg = await fetchJSON(requestUrl);
        process.env.TMDB_CONFIG = tmdbCfg;
        return tmdbCfg;
      } catch (err) {
        return handleError(err.status_message);
      }
    })(),
  };
};

export default getAppConfig;



