// @flow
import React from 'react';
import { NavLink } from 'react-router-dom';
import { multiClass } from 'utils/utils';
import * as styles from './Header.scss';

const Header = ({ isNavigationVisible, onNavigationBtnClick }: HeaderProps) => (
  <header className={styles.header}>
    <NavLink
      className={styles.headerLogo}
      exact
      to="/now-playing"
    >
      RTMDBFE
    </NavLink>
    <div
      role="button"
      tabIndex={0}
      onClick={onNavigationBtnClick}
      className={multiClass(
        styles.headerNavBtn,
        { clsName: styles.headerNavBtnActive, isActive: isNavigationVisible },
        'js-menu',
      )}
      aria-label="Navigation"
    >
      <span className={multiClass(styles.headerNavBtnItem, styles.headerNavBtnItemOne)} />
      <span className={multiClass(styles.headerNavBtnItem, styles.headerNavBtnItemTwo)} />
      <span className={multiClass(styles.headerNavBtnItem, styles.headerNavBtnItemThree)} />
    </div>
  </header>
);

export default Header;



