// @flow
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Switch, Route, Redirect, withRouter } from 'react-router-dom';
import { hideNavigation } from 'actions/uiActions';
import PrivatePagesLayout from 'components/PrivatePagesLayout';
import NowPlayingPage from 'components/NowPlayingPage';
import PopularPage from 'components/PopularPage';
import TopRatedPage from 'components/TopRatedPage';
import UpcomingPage from 'components/UpcomingPage';
import SearchPage from 'components/SearchPage';
import NoMatchPage from 'components/NoMatchPage';

class PrivatePages extends Component<PrivatePageProps> {
  componentDidMount() {
    this.navigationEl = document.querySelector('nav');
    document.addEventListener('click', this.handleDocumentClick);
  }

  componentWillUnmount() {
    document.removeEventListener('click', this.handleDocumentClick);
  }

  navigationEl = null;

  handleDocumentClick = (e: MouseEvent) => {
    const target = (e.target: any);
    if (
      !this.navigationEl ||
      this.navigationEl.contains(target) ||
      target.classList.contains('js-menu') ||
      target.parentNode.classList.contains('js-menu')
    ) return;
    this.props.handleHideNavigation();
  };

  render() {
    if (window.location.pathname === '/') {
      return <Redirect to="/now-playing" />;
    }
    return (
      <PrivatePagesLayout>
        <Switch>
          <Route path="/now-playing" component={NowPlayingPage} />
          <Route path="/popular" component={PopularPage} />
          <Route path="/top" component={TopRatedPage} />
          <Route path="/upcoming" component={UpcomingPage} />
          <Route path="/search" component={SearchPage} />
          <Route component={NoMatchPage} />
        </Switch>
      </PrivatePagesLayout>
    );
  }
}

const mapDispatchToProps = (dispatch: Function) => ({
  handleHideNavigation: () => dispatch(hideNavigation()),
});

export default withRouter(connect(undefined, mapDispatchToProps)(PrivatePages));



