import { SEARCH_MOVIE } from 'constants/actionTypes';
import { createTmdbUrl, fetchJSON, handleError } from 'utils/utils';

export function searchMovies(query, page) {
  const requestUrl = createTmdbUrl(`/search/movie?query=${query}&page=${page}`);

  return {
    type: SEARCH_MOVIE,
    payload: (async () => {
      try {
        const searchRes = await fetchJSON(requestUrl);
        return {
          movies: searchRes.results,
          pages: searchRes.total_pages,
        };
      } catch (err) {
        return handleError(err.status_message);
      }
    })(),
  };
}

export default searchMovies;



